.background {
    background-image: linear-gradient(to right, rgba(62, 56, 56, 0.3), rgba(0, 0, 0, 0.3)), 
    url('/images/mount.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height:1000px;
    // filter: blur(4px);
    // margin-top: 3px;
    // margin-left: 3px;
    // margin-right: 3px;
    // margin-bottom: 3px;

    @media screen and (min-width: 768px) {
        width: 768px;
        height: 679px;
        background-image: url('/images/sky.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        margin-top: 5px;
        margin-left: 10px;
        margin-right: 10px;
        margin-bottom: 5px;
        border-radius: 13px;
        filter: none;

    }

    @media screen and (min-width: 1200px) {
        width: 1200px;
        height: 679px;
        background-image: linear-gradient(to right, rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1)),
        url('/images/sky.jpg');
        // background-image: url('/images/sky.jpg');
        // filter: none;

    }
}

